<template>
    <div class="content-wrap">
        <div class="search-wrap">
            <el-row
                type="flex"
                :gutter="10"
                style="height:52px;"
            >
                <el-col :span="5" >
                    <span class="item-text">应用名称：</span>
                    <el-input
                        class="items"
                        placeholder="请输入应用名称"
                        v-model="appName" />
                </el-col>
                <el-col :span="5" >
                    <span class="item-text">APPID：</span>
                    <el-input
                        class="items"
                        placeholder="请输入APPID"
                        v-model="packageName" />
                </el-col>
                <el-col :span="5" >
                    <span class="item-text">上传人：</span>
                    <el-input
                        class="items"
                        placeholder="请输入手机号"
                        v-model="phoneNumber" />
                </el-col>
                <el-col :span="5" >
                    <span class="item-text">上传时间：</span>
                    <SelectTime class="items" ref="selectUploadTime" @selectTime="selectUploadTime" :type="'datetimerange'" />
                </el-col>
                <el-col :span="5" >
                    <span class="item-text">修改时间：</span>
                    <SelectTime class="items" ref="selectModifyTime" @selectTime="selectModifyTime" :type="'datetimerange'" />
                </el-col>
                <el-button type="primary" style="height:40px" @click="search">查询</el-button>
                <el-button style="height:40px" @click="reset">重置</el-button>
            </el-row>
        </div>
        <div class="table-wrap">
            <el-table
                :data="tableData"
                border
                :header-cell-style="{textAlign: 'center'}"
                :cell-style="{ textAlign: 'center' }"
                style="width: 100%">
                    <el-table-column
                        prop="app_name"
                        label="应用名称"
                        width="180">
                    </el-table-column>
                    <el-table-column
                        prop="file_name"
                        label="APK名称"
                        width="180">
                    </el-table-column>
                    <el-table-column
                        prop="version_name"
                        label="版本号">
                    </el-table-column>
                    <el-table-column
                        prop="package_name"
                        label="APPID">
                    </el-table-column>
                    <el-table-column
                        label="文件大小">
                        <template slot-scope="scope">
                            <span>{{scope.row.file_size | formatterByte}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="上传时间">
                        <template slot-scope="scope">
                            <span>{{new Date(scope.row.create_time) | getTime}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="修改时间">
                        <template slot-scope="scope">
                            <span>{{new Date(scope.row.modify_time) | getTime}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="user_mobile_phone"
                        label="上传人">
                    </el-table-column>
                    <el-table-column
                        prop="install_count"
                        label="装机数">
                    </el-table-column>
                    <el-table-column
                        label="操作">
                        <template slot-scope="scope">
							<el-button
								type="text"
								size="mini"
								@click="handleDel(scope.$index, scope.row)"
								>删除</el-button
							>
						</template>
                    </el-table-column>
            </el-table>
            <Pagination 
                style="margin:10px 0 20px 0;text-align:right;"
                @handleCurrentChange="handleCurrentChange"
                @handleSizeChange="handleSizeChange"
                :total="totalNum"
                :currentPage="currentPage"
                :pageSize="pageSize"
            />
        </div>
    </div>
</template>
<script>
import { appList } from '../../api/resources';
import { parseTime, formatByte } from '../../utils/index';
import { Pagination, SelectTime } from '../../components';
export default {
    data() {
        return{
            appName: '',
            packageName: '',
            phoneNumber: '',
            startCreateTime: '',
            endCreateTime: '',
            startModifyTime: '',
            endModifyTime: '',
            tableData: [],
            currentPage: 1,
            totalNum: 0,
            pageSize: 10
        }
    },
    mounted() {
        // this.startCreateTime = new Date().getTime();
        // this.endCreateTime = new Date().getTime() - 1000*60*60*24*30;
        // this.startModifyTime = new Date().getTime();
        // this.endModifyTime = new Date().getTime() - 1000*60*60*24*30;
        this.getList();
    },
    filters: {
        getTime(time) {
			return parseTime(time);
		},
        formatterByte(byte) {
            return formatByte(byte)
        }
    },
    methods: {
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList();
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getList();
        },
        search() {
            this.currentPage = 1;
            this.pageSize = 10;
            this.getList()
        },
        reset() {
            this.appName = '';
            this.packageName  = "";
            this.phoneNumber = '';
            this.currentPage = 1;
            this.pageSize = 10;
            this.$refs.selectUploadTime.resetTimes();
            this.$refs.selectModifyTime.resetTimes();
            this.getList();
        },
        selectUploadTime(val) {
            this.startCreateTime = val.startTime;
            this.endCreateTime = val.endTime
        },
        selectModifyTime(val) {
            this.startModifyTime = val.startTime;
            this.endModifyTime = val.endTime
        },
        getList() {
            let params = new Object();
            params.appName = this.appName;
            params.packageName  = this.packageName;
            this.startCreateTime ? params.startCreateTime = this.startCreateTime : null;
            this.endCreateTime ? params.endCreateTime = this.endCreateTime : null;
            this.startModifyTime ? params.startModifyTime = this.startModifyTime : null;
            this.endModifyTime ? params.endModifyTime = this.endModifyTime : null;
            params.phoneNumber = this.phoneNumber;
            params.currentPage = this.currentPage;
            params.pageSize = this.pageSize;
            appList(params)
            .then(res => {
                if (res.code == 200) {
                    this.tableData = res.data.list;
                    this.totalNum = res.data.total_number;
                }
            })
            .catch(error => {
                console.log(error)
            });
        },
        handleDel() {}
    },
    components: {
        Pagination, SelectTime
    }
}
</script>
<style lang="less" scoped>
.content-wrap{
    padding: 25px;
    .search-wrap{
        padding: 20px;
        border: 1px solid #ddd;
        .items{
            width: calc(100% - 100px);
        }
        .item-text{
            width: 80px;
            text-align: left;
            display: inline-block;
        }
    }
    .table-wrap{
        margin-top: 20px;
    }
}
</style>


