import {get, post, getImage, postImg } from '@/utils/request';
import {baseURL} from './index';

export const appList = params => {
    return post(`${baseURL}/appManage/appList`, params).then(
        res => res.data
    );
};

export const fileList = params => {
    return post(`${baseURL}/fileManage/fileList`, params).then(
        res => res.data
    );
};


